import React from "react";
import title from '../images/hero/cryptoisland.png'
import gsap from 'gsap'
import ScrollTrigger from "gsap/dist/ScrollTrigger"

const Hero = () => {
    gsap.registerPlugin(ScrollTrigger)


    gsap.to(".c",{
        scrollTrigger:{
            trigger: ".c",
            start: "20% 100px",
            end: "20% 100px",
            toggleActions: "restart pause reverse pause",
            markers: true,
            scrub: 1,
        },
        scaleX: 1.2
    })
  return (
    <>
      <div className="flex items-center justify-center py-12 bg-[url('images/backgrounds/bg-intro.webp')] h-[100vh] bg-cover bg-center bg-no-repeat md:pb-24">'
        <div className="c">
            <img className="w-[800px]" src={title} alt="/" />
        </div>
      </div>
    </>
  );
};

export default Hero;
